const app = getApp();
import uniRate from '@/componets/wyc-rate/uni-rate.vue';
export default {
	components: { uniRate },
	data() {
		return {
			historyList: [],
			hotList: [],
			inputValue: '',
			storageFlag: false, // 显示搜索记录标志位
			mask: true,
			imgUrl: '',
			itemList: [],
		};
	},
	onLoad() {
		this.imgUrl = this.$helper.imgUrl;
		this.openLocationsercher();
		this.getHot();
	},
	methods: {
		// 热门搜索
		getHot() {
			uni.request({
				url: `${this.$helper.requestUrl}search/getHotSearch`,
				method: 'POST',
				header: {
					authorization: app.globalData.token
				},
				success: res => {
					res = this.$helper.null2str(res.data);
					console.log(res, '-------------------');
					uni.hideToast();
					if (res.status_code == 'ok') {
						this.hotList = res.data;
						// this.getPay(res.data);
					} else {
						uni.showToast({
							title: res.message,
							icon: 'none'
						});
					}
				}
			});
		},
		goHot(e) {
			this.inputValue = e
			this.storageFlag = true;
			this.mask = false;
			this.itemList = [];
			this.getListData()
		},
		// 获取输入框的输入信息
		bindInput(e) {
			this.inputValue = e.detail.value;
			if (this.inputValue == '') {
				this.mask = true;
			}
		},
		// // 清楚缓存历史并关闭历史搜索框
		clearSearchStorage() {
			uni.removeStorageSync('searchData');
			this.historyList = [];
			this.storageFlag = false;
		},
		// // 点击缓存搜索列表
		tapSercherStorage(e) {
			console.log(e)
			var index = e;
			this.inputValue = index;
			this.storageFlag = true;
			this.mask = false;
			this.itemList = [];
			this.getListData();
		},
		// //添加搜索记录并搜索
		setSearchStorage(e) {
			this.inputValue = e;
			if (this.inputValue != '') {
				//将搜索记录更新到缓存
				var searchData = this.historyList;
				searchData.unshift(this.inputValue);
				searchData = [...new Set(searchData)];
				searchData = searchData.splice(0, 6);
				console.log(searchData);
				uni.setStorageSync('searchData', searchData);
				this.storageFlag = true;
				this.mask = false;
				this.openLocationsercher();
				this.itemList = [];
				this.getListData();
			}
		},
		// // 打开历史记录列表
		openLocationsercher() {
			console.log(uni.getStorageSync('searchData'));
			this.historyList = uni.getStorageSync('searchData') || []; //调用API从本地缓存中获取数据
			if (this.historyList == '') {
				this.storageFlag = false;
			} else {
				this.storageFlag = true;
			}
		},
		getListData() {
			uni.showLoading({
				title: '加载中...',
			})
			uni.request({
				url: `${this.$helper.requestUrl}search/search`,
				header: {
					authorization: app.globalData.token
				},
				method: 'POST',
				data: {
					search: this.inputValue
				},
				success: res => {
					uni.hideLoading()
					res = this.$helper.null2str(res.data);
					if (res.status_code == 'ok') {
						this.itemList = res.data;
					} else {
						this.itemList = '';
					}
				}
			});
		},
		goDetail(id,e){
			console.log(id,e)
			let url = ''
			if(e == 'curricula'){
				url = '/pages/indexCourse'
			}else{
				url = '/pages/indexDataDetail'
			}
			uni.navigateTo({
				url:`${url}?id=${id}&type=${e}`
			})
		}
		
	}
};
